




































































import { Vue, Component } from 'vue-property-decorator';
import Sider from '../components/Sider.vue';
import crumbs from '../components/crumbs.vue';
import * as echarts from 'echarts/core';
import { TooltipComponent, LegendComponent } from 'echarts/components';
import { PieChart } from 'echarts/charts';
import { LabelLayout } from 'echarts/features';
import { CanvasRenderer } from 'echarts/renderers';
echarts.use([
  TooltipComponent,
  LegendComponent,
  PieChart,
  CanvasRenderer,
  LabelLayout,
]);

const columns = [
  {
    title: '产线',
    dataIndex: 'column1',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '总数',
    dataIndex: 'column2',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '闭环数',
    dataIndex: 'column3',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '未闭环数',
    dataIndex: 'column4',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '超期数',
    dataIndex: 'column5',
    ellipsis: false,
    align: 'center',
  },
];

const columns2 = [
  {
    title: '序号',
    dataIndex: 'column1',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '产线',
    dataIndex: 'column2',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '课题',
    dataIndex: 'column3',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '片区',
    dataIndex: 'column4',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '问题描述',
    dataIndex: 'column5',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '处理进展',
    dataIndex: 'column6',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '负责人',
    dataIndex: 'column7',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '提出人',
    dataIndex: 'column8',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '提出时间',
    dataIndex: 'column9',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '解决时间',
    dataIndex: 'column10',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '是否闭环',
    dataIndex: 'column11',
    ellipsis: false,
    align: 'center',
  },
];
@Component({
  components: { Sider, crumbs },
})
export default class Detail extends Vue {
  itemList = [
    {
      name: 'KKOI1ASKD',
      value: 1,
    },
    {
      name: 'KKOI3ASKD',
      value: 2,
    },
    {
      name: 'KKOI2ASKD',
      value: 3,
    },
  ];
  option = {
    color: ['#8680d8', '#efd358', '#e86c60'],
    show: false,
    tooltip: { trigger: 'item' },

    legend: { show: false, width: 198, height: 198, top: '0%', left: 'center' },
    series: [
      {
        type: 'pie',
        radius: '90%',
        center: ['50%', '50%'],
        itemStyle: {
          borderRadius: 5,
          borderColor: '#fff',
          borderWidth: 1,
        },
        label: {
          show: false,
        },
        emphasis: {
          label: {
            show: false,
          },
        },
        data: [
          { value: 1048, name: 'Search Engine' },
          { value: 735, name: 'Direct' },
          { value: 580, name: 'Email' },
        ],
      },
    ],
  };
  current = 1;
  name = '看板';
  columns = columns;
  data = [
    { key: '1', column1: '1', column2: '1', column3: '1', column4: 90 },
    { key: '2', column1: '2', column2: '2', column3: '2', column4: 2 },
    { key: '3', column1: '3', column2: '3', column3: '3', column4: 12 },
    { key: '4', column1: '4', column2: '4', column3: '4', column4: 65 },
    { key: '5', column1: '5', column2: '5', column3: '5', column4: 74 },
    { key: '6', column1: '6', column2: '6', column3: '6', column4: 86 },
  ];
  mounted() {
    const charts = echarts.init(
      document.getElementById('board-pie') as HTMLElement
    );
    charts.setOption(this.option);
  }
  get path() {
    return ['网站首页', '交流分享', this.name];
  }
  onShowSizeChange(page, pageSize) {
    console.log(page, pageSize);
  }
  change(e) {
    this.columns = columns;
    switch (e.code) {
      case 'category2':
        this.columns = columns2;
        break;
    }
    this.name = e.name;
  }
}
